import { instantiate } from "./instantiate";
import { updateInputs, wasmSetJoypadState } from "./joypad";

let screenCanvasOffscreen: any = null;
let backgroundCanvasOffscreen: any = null;
let windowCanvasOffscreen: any = null;
let tileDataCanvasOffscreen: any = null;
let playIntervalId: NodeJS.Timer | null = null;
let wasmExports: any = null;

const wasmUrl =
  process.env.NODE_ENV === "development" ? "/debug.wasm" : "/release.wasm";

async function init() {
  try {
    wasmExports = await instantiate(
      await WebAssembly.compileStreaming(fetch(wasmUrl)),
      {
        env: undefined,
      }
    );
    postMessage({
      type: "INIT",
      isLoading: false,
      isError: false,
      error: null,
    });
  } catch (err: any) {
    postMessage({
      type: "INIT",
      isLoading: false,
      isError: true,
      error: err?.message ? err.message : "",
    });
  }
}

init();

onmessage = function (e) {
  const type = e.data.type;

  switch (type) {
    case "PLAY": {
      play(e.data.debugMode);
      break;
    }
    case "PLAY_TICK": {
      playN(e.data.tickNumber, "tick");
      break;
    }
    case "PLAY_FRAME": {
      playN(e.data.frameNumber, "frame");
      break;
    }
    case "PAUSE": {
      pause();
      break;
    }
    case "RESET": {
      reset();
      break;
    }
    case "TRANSFER": {
      transferOffscreen(e.data.offscreen, e.data.canvasType);
      break;
    }
    case "LOAD_ROM": {
      loadRom(e.data.romBuffer);
      break;
    }
    case "LOAD_SAVE": {
      loadSave(e.data.saveBuffer);
      break;
    }
    case "JOYPAD": {
      updateInputs(e.data.key, e.data.shouldActivate);
      break;
    }
    default: {
      console.log("Message received from main script");
      break;
    }
  }
};

function play(debugMode: boolean) {
  if (!wasmExports.hasCartridge())
    return console.error("Load rom before playing");
  if (playIntervalId) return console.error("ALREADY PLAYING");

  postMessage({ type: "PLAY" });
  playIntervalId = setInterval(() => {
    wasmSetJoypadState(wasmExports);
    wasmExports.execFrame(1);

    if (debugMode) {
      const { registers, memory } = getValuesFromWasm();
      // @ts-ignore
      postMessage({ type: "DEBUG_VALUES", memory, registers }, [
        memory.buffer,
        registers.buffer,
      ]);
    } else {
      const screen = wasmExports.getScreen();

      if (screenCanvasOffscreen) {
        const imageData = new ImageData(screen, 160, 144);
        screenCanvasOffscreen.putImageData(imageData, 0, 0);
      } else {
        // @ts-ignore
        postMessage({ type: "SCREEN", screen }, [screen.buffer]);
      }
    }
  }, 1000 / 60);
}

function playN(n = 1, type: "tick" | "frame") {
  if (!wasmExports.hasCartridge())
    return console.error("Load rom before playing");
  if (playIntervalId) return console.error("ALREADY PLAYING");

  postMessage({ type: "PLAY" });

  if (type === "tick") wasmExports.execTick(n);
  else wasmExports.execFrame(n);

  const { registers, memory } = getValuesFromWasm();
  // @ts-ignore
  postMessage({ type: "DEBUG_VALUES", memory, registers }, [
    memory.buffer,
    registers.buffer,
  ]);
  postMessage({ type: "PAUSE" });
}

function pause() {
  if (!playIntervalId) {
    console.log("ALREADY PAUSED");
    return;
  }
  clearInterval(playIntervalId);
  playIntervalId = null;
  postMessage({ type: "PAUSE" });
}

function reset() {
  pause();
  wasmExports.reset();

  const { registers, memory } = getValuesFromWasm();

  // @ts-ignore
  postMessage({ type: "DEBUG_VALUES", memory, registers }, [
    memory.buffer,
    registers.buffer,
  ]);
}

function transferOffscreen(offscreen: any, canvasType: string) {
  if (canvasType === "screen") {
    const screenBuffer = wasmExports.getScreen();
    screenCanvasOffscreen = offscreen.getContext("2d");
    const imageData = new ImageData(screenBuffer, 160, 144);
    screenCanvasOffscreen.putImageData(imageData, 0, 0);
  }
  if (canvasType === "background") {
    const backgroundBuffer = wasmExports.getBackground();
    backgroundCanvasOffscreen = offscreen.getContext("2d");
    const imageData = new ImageData(backgroundBuffer, 256, 256);
    backgroundCanvasOffscreen.putImageData(imageData, 0, 0);
  }
  if (canvasType === "window") {
    const windowBuffer = wasmExports.getWindow();
    windowCanvasOffscreen = offscreen.getContext("2d");
    const imageData = new ImageData(windowBuffer, 256, 256);
    windowCanvasOffscreen.putImageData(imageData, 0, 0);
  }
  if (canvasType === "tileData") {
    const wholeTileData = wasmExports.getWholeTileData();
    tileDataCanvasOffscreen = offscreen.getContext("2d");
    const imageData = new ImageData(wholeTileData, 192, 128);
    tileDataCanvasOffscreen.putImageData(imageData, 0, 0);
  }
}

function loadRom(buffer: Uint8Array) {
  wasmExports.loadRom(buffer);
}

function loadSave(buffer: Array<number>) {
  wasmExports.loadSave(buffer);
  const { registers, memory } = getValuesFromWasm();
  // @ts-ignore
  postMessage({ type: "DEBUG_VALUES", memory, registers }, [
    memory.buffer,
    registers.buffer,
  ]);
}

function getValuesFromWasm() {
  const screenBuffer = wasmExports.getScreen();
  const windowBuffer = wasmExports.getWindow();
  const backgroundBuffer = wasmExports.getBackground();
  const wholeTileData = wasmExports.getWholeTileData();
  const memory = wasmExports.getMemory();
  const registers = wasmExports.getRegisters();

  if (screenCanvasOffscreen) {
    const imageData = new ImageData(screenBuffer, 160, 144);
    screenCanvasOffscreen.putImageData(imageData, 0, 0);
  }

  if (backgroundCanvasOffscreen) {
    const imageData = new ImageData(backgroundBuffer, 256, 256);
    backgroundCanvasOffscreen.putImageData(imageData, 0, 0);
  }

  if (windowCanvasOffscreen) {
    const imageData = new ImageData(windowBuffer, 256, 256);
    windowCanvasOffscreen.putImageData(imageData, 0, 0);
  }

  if (tileDataCanvasOffscreen) {
    const imageData = new ImageData(wholeTileData, 192, 128);
    tileDataCanvasOffscreen.putImageData(imageData, 0, 0);
  }

  return {
    memory,
    registers,
  };
}
