const inputs = {
  select: false,
  start: false,
  a: false,
  b: false,
  up: false,
  down: false,
  left: false,
  right: false,
};

export function updateInputs(key: string, shouldActivate: boolean) {
  switch (key) {
    case "down": {
      inputs.down = shouldActivate;
      break;
    }
    case "up": {
      inputs.up = shouldActivate;
      break;
    }
    case "left": {
      inputs.left = shouldActivate;
      break;
    }
    case "right": {
      inputs.right = shouldActivate;
      break;
    }
    case "start": {
      inputs.start = shouldActivate;
      break;
    }
    case "select": {
      inputs.select = shouldActivate;
      break;
    }
    case "a": {
      inputs.a = shouldActivate;
      break;
    }
    case "b": {
      inputs.b = shouldActivate;
      break;
    }
  }
}

export function wasmSetJoypadState(wasmExports: any) {
  wasmExports.setJoypadState(
    inputs.up ? 1 : 0,
    inputs.right ? 1 : 0,
    inputs.down ? 1 : 0,
    inputs.left ? 1 : 0,
    inputs.a ? 1 : 0,
    inputs.b ? 1 : 0,
    inputs.select ? 1 : 0,
    inputs.start ? 1 : 0
  );
}
